
.typography-demo {
  font-family: var(--primary-font-regular);
}
.typography-demo h1, .typography-demo .h1,
  .typography-demo h2, .typography-demo .h2,
  .typography-demo h3, .typography-demo .h3,
  .typography-demo h4, .typography-demo .h4,
  .typography-demo h5, .typography-demo .h5,
  .typography-demo h6, .typography-demo .h6 {
    font-family: var(--headline-font);
    margin-bottom: 0;
}
.typography-demo h1, .typography-demo .h1 {
    font-size: 3.25rem;
    font-weight: 700;
}
.typography-demo h2, .typography-demo .h2 {
    font-size: 2.75rem;
    font-weight: 700;
}
.typography-demo h3, .typography-demo .h3 {
    font-size: 2.25rem;
    font-weight: 700;
}
.typography-demo h4, .typography-demo .h4 {
    font-size: 1.75rem;
    font-weight: 500;
}
.typography-demo h5, .typography-demo .h5 {
    font-size: 1.25rem;
    font-weight: 500;
}
.typography-demo h6, .typography-demo .h6 {
    font-size: .875rem;
    font-weight: 500;
}
.typography-demo p a,
  .typography-demo u {
    text-decoration: underline;
    -webkit-text-decoration-skip: ink;
            text-decoration-skip: ink;
}
.module-section--font-demo {
  font-size: .75em;
}
.module-section--font-demo p {
    margin-bottom: 0;
}
.module-section--font-demo .module--demo--info {
    background: transparent;
    border-top: 1px solid var(--light-grey);
    display: block;
    margin-top: 0;
    padding-top: 10px;
    padding-bottom: 10px;
}
.module-section--font-demo .module--demo--info p {
      display: block;
      font-family: inherit;
      font-weight: inherit;
      font-size: 1.5rem;
      letter-spacing: .1ch;
      max-width: 34rem;
      word-break: break-all;
}
.module-section--font-demo .module--demo--info p:nth-child(2) {
      padding: 0;
}
.module-section--primary-font .module:nth-of-type(1),
.module-section--primary-font .module:nth-of-type(1) .h2 {
  font-family: var(--primary-font-light);
}
.module-section--primary-font .module:nth-of-type(2),
.module-section--primary-font .module:nth-of-type(2) .h2 {
  font-family: var(--primary-font-regular);
}
.module-section--primary-font .module:nth-of-type(3),
.module-section--primary-font .module:nth-of-type(3) .h2 {
  font-family: var(--primary-font-medium);
}
.module-section--headline-font .module,
.module-section--headline-font .module .h2 {
  font-family: var(--headline-font);
}
.module-section--base-font .module,
.module-section--base-font .module .h2 {
  font-family: var(--base-font);
}
.module-section--base-font .module .h2 {
  font-size: 1rem;
  font-weight: normal;
  margin-bottom: 1rem;
}
.module-section--serif-font .module {
  font-family: var(--serif-font);
}
.module-section--serif-font .module .h2 {
  font-family: var(--base-font);
  font-size: 1rem;
  font-weight: normal;
  margin-bottom: 1rem;
}
.drop-caps {
  clear: both;
}
.drop-caps:first-letter {
    color: var(--red);
    float: left;
    font-size: 4rem;
    line-height: .8;
    padding-top: .05em;
    padding-right: .5rem;
    padding-left: .125rem;
}
.drop-caps--serif::first-letter {
    font-family: var(--serif-font);
}
.text-highlight {
  background-color: var(--yellow);
  color: var(--dark);
  padding: .25em;
}
blockquote {
  background: var(--lighter);
  border-left: 4px solid var(--red-film);
  font-family: var(--serif-font);
  font-style: italic;
  margin-top: 1rem;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 1rem;
  padding: 1rem;
}



