
@charset "UTF-8";
.sim-selection {
  display: inline-block;
}
.sim-selection label {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: baseline;
        -ms-flex-align: baseline;
            align-items: baseline;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    margin: 0;
}
.sim-selection label.disabled {
      cursor: not-allowed;
      color: inherit;
}
.sim-selection input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: none;
    font: inherit;
    cursor: inherit;
    color: inherit;
    width: 1em;
    height: 1em;
    margin: 0 1em 0 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.sim-selection input::before {
      content: var(--selection-unchecked-icon, var(--icon-circle-thin, ""));
      font-family: var(--icon-font, "FontAwesome");
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
      will-change: transform;
      transition: -webkit-transform var(--ms, var(--default-ms, 350ms)) ease-out;
      -webkit-transition: -webkit-transform var(--ms, var(--default-ms, 350ms)) ease-out;
      transition: transform var(--ms, var(--default-ms, 350ms)) ease-out;
      transition: transform var(--ms, var(--default-ms, 350ms)) ease-out, -webkit-transform var(--ms, var(--default-ms, 350ms)) ease-out;
}
.sim-selection input:active:not(:disabled)::before {
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
}
.sim-selection input:checked {
      color: var(--selection-color, var(--action));
}
.sim-selection input:checked::before {
        content: var(--selection-checked-icon, var(--icon-check-circle, ""));
}
.sim-selection label.disabled {
    opacity: .5;
}
.sim-selection input::before {
    opacity: .4;
}
.sim-selection input:checked::before {
    opacity: 1;
}



