
.sim-button,
button {
  font-size: inherit;
  font-family: inherit;
  border: 0;
  padding: .5em 1em;
  border-radius: .3em;
  cursor: pointer;
  -webkit-transition: 300ms ease-in-out;
  transition: 300ms ease-in-out;
  background: var(--line);
  color: inherit;
  opacity: 1;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.sim-button + .sim-button,
  .sim-button + button,
  button + .sim-button,
  button + button {
    margin-left: .5em;
}
.sim-button.active,
  button.active {
    background: var(--active);
    color: var(--lighter);
}
.sim-button:hover,
  button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.sim-button[disabled],
  button[disabled] {
    background: var(--light-grey);
    opacity: .3;
    cursor: not-allowed;
}
.sim-button[disabled].sim-button--cancel,
    button[disabled].sim-button--cancel {
      color: inherit;
}
.sim-button[disabled]:hover,
    button[disabled]:hover {
      -webkit-transform: none;
              transform: none;
}
.sim-button--primary,
  button--primary {
    background: var(--action);
    color: var(--lighter);
}
.sim-button--secondary,
  button--secondary {
    background: var(--light);
    color: var(--action);
}
.sim-button--secondary:hover,
    button--secondary:hover {
      background: var(--edge);
}
.sim-button--link,
  button--link {
    background-color: transparent;
    color: var(--action);
    border-radius: 0;
}
.sim-button--cancel,
  button--cancel {
    background: var(--mist);
}
.sim-calendar-button {
  background: var(--light);
}



